body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.preview {
    margin-left: 20px;
}

.preview-button {
    background: #3176fb;
    color: white;
    padding: 10px;
    text-transform: uppercase;
    border: 0 none;
    border-radius: 5px;
}

.preview-text-input {
    padding: 10px;
    margin-right: 10px;
}

.preview-instructions {
    margin-top: 10px;
}
.List,
.Header {
  border: 1px solid #d9dddd;
  margin: auto;
}

.List div {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.ListItemEven {
}

.even {
  background-color: #f8f8f0;
}

div.statusReason {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: clip;
  text-align: left;
  -webkit-justify-content: left;
          justify-content: left;
}

div.fileName{
  white-space: nowrap;
  overflow: hidden;
  text-overflow: clip;
  /*text-align: left;*/
  /*justify-content: left;*/
}

.errors-grid {
  margin: 30px 0;
}

.App {
  text-align: center;
}

.App-link, .App a {
  color: #3176fb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.App-navigation {
  list-style: none;
    margin-right: 30px;
}

.App-navigation li {
  display: inline-block;
  margin-right: 10px;
}

.clickable {
  cursor: pointer;
}

.align-left {
  text-align: left;
}
